@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@100;300;400;500;700;900&display=swap);
html,
body {
  background: rgb(0, 92, 184);
  background: linear-gradient(
      135deg,
      rgb(0, 92, 184) 0%,
      rgba(0, 45, 98, 1) 50%,
      rgba(0, 13, 28, 1) 100%
    )
    fixed;
  font-family: "Noto Sans JP", sans-serif;
  font-weight: 400;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  margin: 0;
  padding: 0;
}

.App {
  color: #fff;
  text-align: center;
}

.Home {
  text-align: left;
}

.UkanaHeader,
.UkanaHeaderJp {
  display: inline-block;
  font-size: 5rem;
  font-weight: 500;
  margin: 0;
  margin-left: 1rem;
}

.UkanaHeaderJp {
  color: #bf1919;
  font-weight: 300;
}

.Subheader {
  margin: 0;
  margin-left: 1rem;
  font-style: italic;
}

.SubheaderStart {
  font-weight: 500;
  margin-left: 1rem;
}

.Navbar {
  background: #090645;
  padding: 0.5rem 0;
  position: relative;
  text-align: left;
  width: auto;
}

a {
  color: #fff;
  text-decoration: none;
}

ul {
  margin: 0;
  padding: 0;
}

li {
  display: inline-block;
  list-style-type: none;
  margin-right: 2rem;
  text-decoration: none;
}

.UkanaTitle {
  color: #3e70dc;
  margin-left: 2rem;
  margin-top: 0;
  margin-bottom: 0;
}

.NavLink {
  margin: 0;
}

.non-home {
  font-size: 1.2rem;
}

.KanjiCard {
  background: #fff;
  border-radius: 20px;
  color: #000;
  margin: auto;
  margin-top: 1rem;
  padding: 0.5rem;
  text-align: left;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

.KanjiCardHeader {
  text-align: center;
}

.KanjiCardKanji {
  font-size: 5rem;
  margin: 0;
}

.KanjiCardMeaning {
  margin: 0;
}

